import { Card, Grid, styled, useTheme, Box, Button, Menu, MenuItem, LinearProgress, TextField } from '@mui/material';
import { Fragment, useEffect, useState } from 'react';
import useSettings from 'app/hooks/useSettings';
import useMediaQuery from '@mui/material/useMediaQuery';

const chunk = (arr, len) => {
    var chunks = [],
        i = 0,
        n = arr.length;
    while (i < n) {
        chunks.push(arr.slice(i, i += len));
    }
    return chunks;
}

const NumberButton = styled(Button)(() => ({
    fontSize: '26px'
}))



const Analytics = ({ auto, dataArr, pageSize, unmask, unmaskValue }) => {
    const matches = useMediaQuery('(min-width:600px)');
    const { palette } = useTheme();
    const [masterData, setMasterData] = useState(dataArr)
    const [data, setData] = useState([])
    const [special, setSpecial] = useState([])
    const [completed, setCompleted] = useState(0)
    const [page, setPage] = useState(0)
    const offsetPage = (val) => {
        setPage(page + val)
    }


    const unmaskFunc = (orignal, v) => {
        if (v) {
            const index = orignal.indexOf(v)
            return v.padStart(index + v.length, 'X') + [...Array(orignal.length - (index + v.length)).keys()].map(e => 'X').join('')

        } else {
            return 'XXXX' + orignal.slice(4)
        }

    }

    useEffect(() => {
        setData(chunk(dataArr, pageSize))
    }, [dataArr, pageSize])

    useEffect(() => {
        function progress() {
            setCompleted((oldCompleted) => {
                if (oldCompleted === 100) {


                    setPage((p) => {

                        if (p < data.length - 1) {
                            return p + 1
                        } else {
                            return 0
                        }
                    })

                    return 0
                }
                const diff = 20
                return Math.min(oldCompleted + diff, 100)
            })
        }

        if (auto) {
            const timer = setInterval(progress, 1000)
            return () => {
                clearInterval(timer)
            }
        }

    }, [data, auto])
    return (
        <Fragment>
            <Box gap="30px" display="flex" flexWrap="wrap" alignItems={"center"} alignContent={"center"} >
                {
                    data.map((chunks, index) => {
                        return index === page && chunks.map((n => <NumberButton
                            variant="outlined"
                            aria-haspopup="true"
                            onClick={() => window?.location.assign(`https://api.whatsapp.com/send?phone=6587413159&text=I%20am%20interested%20in%20${n.num}`)}
                        >

                            <Box color={n.num[0] === '9' ? 'red' : 'blue'}>

                                {unmask ? unmaskFunc(n.num, unmaskValue) : n.num}
                            </Box>
                        </NumberButton>))
                    })
                }

            </Box>
            <br />

            <Box>
                <NumberButton
                    variant="outlined"
                    aria-haspopup="true"
                    onClick={() => offsetPage(-1)}
                    disabled={page === 0}
                >Prev</NumberButton>
                {
                    `Page ${page + 1} of ${data.length}`
                }
                <NumberButton
                    variant="outlined"
                    aria-haspopup="true"
                    onClick={() => offsetPage(1)}
                    disabled={page >= data.length - 1}
                >Next</NumberButton>
            </Box>
        </Fragment >
    );
};

export default Analytics;
